export default{
	fontFun:function(){//优化页面
		var imgs = $('[data-src]');
		var Height=$(document).height();
		var doHeight=$(window).clientHeight;
		var Top=$(window).scrollTop();
		for(var i=0;i<imgs.length;i++){
			this.imgtop=imgs[i].offsetTop;
				if(imgs[i].offsetTop<Top+$(window).height()){
					var data=$("img").eq(i).attr("data-src");
		//						console.log(data)
					$("img").eq(i).attr("src",data);
					$("img").eq(i).fadeIn(1000);
				}
		}

	},
	init:function(){/*初始化*/
		var obj=this;
		obj.fontFun();//调用fontFun函数
	}
};


